.contactwrap {
    max-width: 1016px;
    width: 100%;
    margin: auto;
}
.contactWrapp{
        background-color: #EFEFFB;
}
.wrappper{
    /* background-color: red; */
    max-width: 600px !important;
    width: 100%;
    margin: auto !important;
}
.contactwrap h1 {
    padding-top: 64px;
    font-size: 40px;
    font-weight: 600;
    line-height: 60px;
    letter-spacing: 0em;
    text-align: center;
    font-family: 'Inter', sans-serif;
 
}
 
.contactUs {
    display: flex;
    justify-content: space-between;
    padding-bottom: 80px;
    /* background-color: rgb(75, 236, 129); */
    align-items: center;
}
 
 
.contact-img img {
    max-width: 600px;
    width: 100%;
    max-height: 344px;
    height: 100%;
}
 
 
 
 
label {
    font-size: 13px;
    margin-bottom: 34px !important;
}
 
 
 
.roww{
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    max-width: 100%;
    width: 100%;
  }
 
  .form-group{
    width: 47%;
    float: left;
    margin: 0 12px 30px 0;
    border-radius: 8px;
    /* border: 2px solid rgb(90, 90, 90); */
  }
 
 
 
  .form-fields .form-group:last-child {
    width: 47%;
  }
 
  .form-fields input{
    border: none;
    outline:none;
    background: none;
    font-size: 18px;
    color:#555;
    padding: 6px 16px;
    width: 100%;
    margin-top: 16px;
    border: 2px solid rgb(90, 90, 90);
    border-radius: 8px;
  }
 
 
  .roww textarea{
    border: none;
    outline:none;
    background: none;
    font-size: 18px;
    color:#555;
    max-width: 100%;
    width: 100%;
    padding:  16px ;
    height: 90px !important;
    border-radius: 8px;
    margin-top: 16px;
    border: 2px solid rgb(90, 90, 90);
   
  }
 
  .msg{
    max-width: 70%;
    width: 100%;
  }
 
.submitbtn button{
    color: white;
    padding:16px 24px ;

    font-size: 20px;
    border-radius: 30px;
    background-color: rgb(29, 28, 28);
    margin: 50px 12px 32px 0;
}
/* .submitbtn{
    max-width: 50% !important;
    width: 50% !important;
} */
 
.submitbtn button{
        margin-top: 1% !important;

}
 
  .form-fields{
    display: table;
    width: 100%;
  }
 
 
 .roww textarea{
    resize: none;
  }
 
 
 
 
 
 
 
 
@media (max-width: 800px) {
    .row {
        flex-direction: column;
    }
    .roww {
        flex-direction: column;
    }
    .submitbtn button{
        max-width: 100% !important;
        width: 100% !important;
    }
    .submitbtn{
        max-width: 100% !important;
        width: 100% !important;
    }
    .msg{
        max-width: 100%;
        width: 100%;
    }
    .btn .submitbtn {
        width: 318px;
    }
 
    .contactUs {
        align-items:stretch;
    }
   
 
   
}
 
 
 
@media (max-width: 800px) {
    .contactwrap h1 {
        padding-top: 32px !important;
        padding-bottom: 16px !important;
        font-weight: 600;
        font-size: 28px;
        line-height: 34px;
        text-align: center;
        color: #1D1D1D;
    }
 
 
 
}
 
 
 
 
 
@media only screen and (max-width: 900px) {
    .box-wrapper{
        width: 100%;
    }
    .info-wrap,
    .form-wrap{
        width: 100%;
        height: inherit;
        float: none;  
    }
    .info-wrap{
        border-radius: 30px 30px 0px 0px;
    }
    .form-wrap{
        border-radius: 0px 0px 30px 30px;
    }
    .form-group{
        width: 100%;
        float: none;
        margin: 25px 0px;
    }
    .form-fields .form-group:last-child,
    .submit-button{
        width: 100%;
    }
    .submit-button{
        margin: 10px 0px;
    }
   
  }
